* {
  margin: 0;
  padding: 0; }

.modal {
  transition: 0.3s all;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 650px !important;
  height: 700px;
  color: white;
  transform: translate(-50%, -50%);
  background-color: #32393d;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.2);
  padding: 40px;
  outline: "none";
  margin: "auto";
  margin-top: "15rem";
  border-radius: 12px; }
  .modal:focus {
    outline: none; }

.bg-img {
  background: url("http://localhost:8080/assets/landing-background.jpg") no-repeat center center fixed;
  background-size: cover;
  width: 100%;
  height: 100vh; }

.input {
  font-size: 16px; }

.customSignupCard {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 600px;
  width: 650px;
  padding: 40px;
  border-radius: 12px;
  background-color: #32393d;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3); }
